import _ from "lodash";
import { SID } from "@/dict";
import { queryAll, save } from "@/api";
import { setLocalStorageItem } from "@/utils";
import { storeOptions } from "./default";

const tableName = "language_base";
const options = new storeOptions({
  all: {
    api: queryAll,
    params: {
      table: {
        name: tableName,
        no_default_field: true,
        fields: ["id", "key", "native_name"]
      }
    }
  },
  save: {
    api: save,
    tableName,
    success({ commit, state }, record) {
      record = _.find(state.all.data, { id: record.id });
      commit("setValues", {
        ...state.values,
        [record.key]: record.native_name
      });
    }
  }
});

options.state = {
  ...options.state,
  values: {
    account_disabled: "",
    avatar: "",
    base: "",
    character_template: "${min} ~ ${max} ",
    closed: "",
    collapse: "",
    comment: "",
    confirm: "",
    create: "",
    created_at: "",
    created_by_uid: "",
    delete: "",
    description: "",
    detail: "",
    disable: "",
    edit: "",
    enable: "",
    existed: "",
    field: "",
    format: "",
    icon: "",
    input_number_step: "",
    language: "",
    list: "",
    login_expired: "",
    login_logs: "",
    logout: "",
    message: "",
    my_system: "",
    name: "",
    no_quotation_marks: " `  '",
    opened: "",
    operation: "",
    parent_area: "",
    permission: "",
    personal_center: "",
    please_input: "",
    please_select: "",
    precision: "",
    record_not_exist: "",
    reset: "",
    save: "",
    search: "",
    show: "",
    state: "",
    successful: "",
    sure_delete: "",
    system_is_not_yours: "",
    system_stopped: "",
    table: "",
    textarea_row: "",
    total_template: "${total}",
    update: "",
    updated_at: "",
    updated_by_uid: ""
  }
};

options.mutations = {
  ...options.mutations,
  setValues(state, values) {
    setLocalStorageItem("language_base_" + SID, values);
    state.values = {
      ...state.values,
      ...values
    };
  }
};

options.actions = {
  ...options.actions,
  get({ commit, dispatch }, localeId) {
    dispatch("getAll", {
      table: { filters: [[["locale", "=", localeId]]] }
    }).then(({ data }) => {
      const values = {};
      _.forEach(data, item => {
        values[item.key] = item.native_name;
      });
      commit("setValues", values);
    });
  }
};

export default {
  namespaced: true,
  ...options
};
